import { call, put, takeLatest, all } from 'redux-saga/effects';
import { actions } from './slice';
import { ReportHttp } from '../services/report.http';
import { configResponse } from 'utils/request';
import { MockData } from 'mock/DataManagement';
import * as _ from 'lodash';

const reportRequest = new ReportHttp();
const mock = new MockData();

export function* getDataAdminStatistical(api, action) {
  const response = yield call(api.statisRequest, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.getDataAdminStatisticalSuccess(data));
  } catch (error) {
    yield put(actions.getDataAdminStatisticalFailure(_.get(error, 'message')));
  }
}

