export function* ReportFormSaga() {
  yield all([
    yield takeLatest(
      actions.getDataAdminStatistical.type,
      getDataAdminStatistical,
      reportRequest,
    ),
    yield takeLatest(
      actions.getListHistoryTekmediCard.type,
      getListHistoryTekmediCard,
      reportRequest,
    ),
    yield takeLatest(
      actions.getIdPrinterDeal.type,
      getIdPrinterDeal,
      reportRequest,
    ),
    yield takeLatest(
      actions.postCancelDeal.type,
      postCancelDeal,
      reportRequest,
    ),
    yield takeLatest(
      actions.exportFileHistoryCard.type,
      exportFileHistoryCart,
      reportRequest,
    ),
    yield takeLatest(
      actions.exportHistoryCard.type,
      exportHistoryCard,
      reportRequest,
    ),
    yield takeLatest(
      actions.exportHistoryCardPdf.type,
      exportHistoryCardPdf,
      reportRequest,
    ),
    yield takeLatest(
      actions.exportHistoryCardPdfOver.type,
      exportHistoryCardPdfOver,
      reportRequest,
    ),
    yield takeLatest(
      actions.getTransactionChart.type,
      getTransactionChart,
      mock,
    ),
  ]);
}
