/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-script-url */
/* eslint-disable jsx-a11y/anchor-has-content */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect } from 'react';
import * as _ from 'lodash';
import * as fromReport from 'store/report/shared/selectors';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useSelector, useDispatch } from 'react-redux';
import { sliceKey, reducer, actions } from 'store/report/shared/slice';
import { ReportFormSaga } from 'store/report/shared/saga';
import { Line } from '@ant-design/charts';
import { LoadingChart } from './LoadingChart';
import { CurrencyService } from 'services/currency.service';
import DateFnsUtils from '@date-io/date-fns';
import styled from 'styled-components';

const ChartWrapper = styled.div`
  width: 100%;
  > svg,
  > canvas {
    width: 100% !important;
    height: 100% !important;
  }
`;

export const CardChart = () => {
  const dateUtil = new DateFnsUtils();
  useInjectReducer({ key: sliceKey, reducer: reducer });
  useInjectSaga({ key: sliceKey, saga: ReportFormSaga });
  const loading = useSelector(fromReport.selectLoading);
  const dataReport = useSelector(fromReport.selectDataListTransaction);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(actions.getTransactionChart());
  }, []);

  const config = {
    data:
      !_.isEmpty(dataReport) && dataReport.length
        ? dataReport.map(item => {
            return {
              ...item,
              date: dateUtil.format(new Date(item.date), 'yyyy/MM/dd'),
            };
          })
        : [],
    xField: 'date',
    yField: 'price',
    seriesField: 'type',
    xAxis: { type: 'time' },
    yAxis: {
      label: {
        formatter: function formatter(v) {
          return CurrencyService.formatCurrency(v);
        },
      },
    },
    smooth: true,
    animation: {
      appear: {
        animation: 'path-in',
        duration: 5000,
      },
    },
  };

  return (
    <div className="col-md-8">
      <div className="card card-box">
        <div className="card-head">
          <header>Giao dịch thẻ</header>
        </div>
        <div className="card-body no-padding height-9">
          {loading ? (
            <LoadingChart />
          ) : (
            <div className="row">
              <ChartWrapper>
                <Line {...config} />
              </ChartWrapper>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};
