/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-is-valid */

import { createSlice } from 'store/core/@reduxjs/toolkit';
import { AppHelper } from 'utils/app.helper';
import {
  RESPONSE_CONSTANT,
  RESPONSE_MESSAGE,
} from '../constants/report.constant';
export interface ReportState {
  dataListHistoryTekmediCard: any;
  dataListTranSaction: any;
  dataPrinterHistory: any;
  dataAdminStatistical: any;
  dataTransaction: any;
  dataTransactionDetail: any;
  dataTransactionDetailModal: any;
  dataSurvey: any;
  loading: boolean;
  error: any | null;
  success: any;
  filePdf: any;
  filePdfOver: any;
  filePdfCardFee: any;
  filePdfRegister: any;
  filePdfRecharged: any;
  filePdfReturn: any;
}

export const initialState: ReportState = {
  dataListHistoryTekmediCard: {},
  dataListTranSaction: {},
  dataPrinterHistory: {},
  dataAdminStatistical: {},
  dataTransaction: {},
  dataSurvey: {},
  dataTransactionDetail: {},
  dataTransactionDetailModal: {},
  loading: false,
  error: null,
  success: {},
  filePdf: {},
  filePdfOver: {},
  filePdfCardFee: {},
  filePdfRegister: {},
  filePdfRecharged: {},
  filePdfReturn: {},
};

const reportFormSlice = createSlice({
  name: 'report',
  initialState,
  reducers: {
    getListHistoryTekmediCard(state, action: any) {
      state.loading = true;
      state.error = null;
    },

    getListHistoryTekmediCardSuccess(state, action: any) {
      state.dataListHistoryTekmediCard = action.payload;
      state.loading = false;
    },

    getListHistoryTekmediCardFail(state, action: any) {
      state.loading = false;
      state.dataListHistoryTekmediCard = {};
      state.error = action.payload;
    },

    getIdPrinterDeal(state, action: any) {
      state.loading = true;
      state.error = null;
    },

    getIdPrinterDealSuccess(state, action: any) {
      state.dataPrinterHistory = action.payload;
      state.loading = false;
    },

    getIdPrinterDealFail(state, action: any) {
      state.error = action.payload;
      state.loading = false;
    },

    postCancelDeal(state, action: any) {
      state.loading = true;
      state.error = null;
    },

    postCancelDealSuccess(state, action: any) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.HISTORY_CARD.CANCEL_DEAL_SUCCESS,
        message: RESPONSE_MESSAGE.HISTORY_CARD.CANCEL_DEAL_SUCCESS,
      };
    },

    postCancelDealFail(state, action: any) {
      state.loading = false;
      state.error = action.payload;
    },

    exportFileHistoryCard(state, action: any) {
      state.loading = true;
      state.error = null;
    },

    exportFileHistoryCardSuccess(state, action: any) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.HISTORY_CARD.EXPORT_FILE_HISTORY_CARD_SUCCESS,
        message: RESPONSE_MESSAGE.HISTORY_CARD.EXPORT_FILE_HISTORY_CARD_SUCCESS,
        data: action.payload,
      };
    },

    exportFileHistoryCardFail(state, action: any) {
      state.loading = false;
    },

    exportHistoryCardPdf(state, action) {
      state.loading = true;
    },
    exportHistoryCardPdfSuccess(state, action) {
      state.loading = false;
      state.filePdf = action.payload;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.HISTORY_CARD.EXPORT_HISTORY_CARD_PDF_SUCCESS,
        data: action.payload,
        message: RESPONSE_MESSAGE.HISTORY_CARD.EXPORT_HISTORY_CARD_SUCCESS,
      };
    },
    exportHistoryCardPdfFail(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.HISTORY_CARD.EXPORT_HISTORY_CARD_PDF_FAIL,
        message: action.payload,
      };
    },

    exportHistoryCardPdfOver(state, action) {
      state.loading = true;
    },
    exportHistoryCardPdfOverSuccess(state, action) {
      state.loading = false;
      state.filePdfOver = action.payload.overview;
      state.filePdfCardFee = action.payload.card_fee;
      state.filePdfRegister = action.payload.register;
      state.filePdfRecharged = action.payload.recharged;
      state.filePdfReturn = action.payload.return;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.HISTORY_CARD.EXPORT_HISTORY_CARD_PDF_SUCCESS,
        message: RESPONSE_MESSAGE.HISTORY_CARD.EXPORT_HISTORY_CARD_SUCCESS,
      };
    },
    exportHistoryCardPdfOverFail(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.HISTORY_CARD.EXPORT_HISTORY_CARD_PDF_FAIL,
        message: action.payload,
      };
    },

    exportHistoryCard(state, action) {
      state.loading = true;
    },
    exportHistoryCardSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.HISTORY_CARD.EXPORT_HISTORY_CARD_SUCCESS,
        data: action.payload,
        message: RESPONSE_MESSAGE.HISTORY_CARD.EXPORT_HISTORY_CARD_SUCCESS,
      };
    },
    exportHistoryCardFail(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.HISTORY_CARD.EXPORT_HISTORY_CARD_FAIL,
        message: action.payload,
      };
    },

    getDataAdminStatistical(state, actions: any) {
      state.loading = true;
      state.error = null;
    },

    getDataAdminStatisticalSuccess(state, action) {
      state.loading = false;
      state.dataAdminStatistical = action.payload;
    },

    getDataAdminStatisticalFailure(state, action) {
      state.error = action.payload;
      state.loading = false;
    },

    getDataSurvey(state) {
      state.loading = true;
      state.error = null;
    },

    getDataSurveySuccess(state, action) {
      state.loading = false;
      state.dataSurvey = action.payload;
    },

    getDataSurveyFailure(state, action) {
      state.error = action.payload;
      state.loading = false;
    },

    getTransactionChart(state) {
      state.loading = true;
      state.error = null;
    },

    getTransactionChartSuccess(state, action) {
      state.loading = false;
      state.dataListTranSaction = action.payload;
    },

    getTransactionChartFail(state, action) {
      state.error = action.payload;
      state.loading = false;
    },

    clearSuccess(state) {
      state.success = {};
    },

    clearDataprint(state) {
      state.dataPrinterHistory = null;
    },
    clearFilePdf(state) {
      state.filePdf = {};
    },
    clearFilePdfOver(state) {
      state.filePdfOver = {};
      state.filePdfCardFee = {};
      state.filePdfRegister = {};
      state.filePdfRecharged = {};
      state.filePdfReturn = {};
    },
  },
});

export const { actions, reducer, name: sliceKey } = reportFormSlice;
