import React from 'react';

export type WidgetBG = 'success' | 'blue' | 'orange' | 'purple';

interface Props {
  background: WidgetBG;
  icon: string;
  title: string;
  dataNumber: number;
  description: string;
}

export function Widget({
  background,
  icon,
  title,
  dataNumber,
  description,
}: Props) {
  return (
    <div className="col-xl-3 col-md-6 col-12">
      <div className={`info-box bg-${background}`}>
        <span className="info-box-icon push-bottom">
          <i className="material-icons">{icon}</i>
        </span>
        <div className="info-box-content">
          <span className="info-box-text">{title}</span>
          <span className="info-box-number">{dataNumber}</span>
          <span>$</span>
          <div className="progress">
            <div className="progress-bar" style={{ width: '50%' }} />
          </div>
          <span className="progress-description">{description}</span>
        </div>
      </div>
    </div>
  );
}
