import { HttpRequest } from 'utils//request';
import { APP_CONSTANT } from 'store/common/constants/common.constant';
import { ReportApiRoutes } from '../constants/http.constant';
export class ReportHttp {
  request: any;
  constructor(endPoint = APP_CONSTANT.API.ENDPOINT) {
    this.request = new HttpRequest(endPoint).request;
  }

  getListHistoryTekmediCard = (data: any): Promise<any> => {
    return this.request.post(
      ReportApiRoutes.TEKMEDI_CARD.GET_LIST_CARD_STATISTICS,
      data,
    );
  };
  getIdPrinterDeal = (id: string): Promise<any> => {
    return this.request.get(
      `${ReportApiRoutes.TEKMEDI_CARD.PRINT_DEAL}?id=${id}`,
    );
  };
  postCancelDeal = (id: string): Promise<any> => {
    return this.request.post(
      `${ReportApiRoutes.TEKMEDI_CARD.CANCEL_DEAL}/${id}`,
    );
  };
  exportFileHistoryCard = ({
    userId,
    userName,
    startDate,
    endDate,
    payment_type_id,
  }) => {
    return this.request.post(ReportApiRoutes.TEKMEDI_CARD.EXPORT_FILE, {
      employee_id: userId,
      employee_name: userName,
      start_date: startDate,
      end_date: endDate,
      payment_type_id,
    });
  };
  exportHistoryCard = (data): Promise<any> => {
    return this.request.post(
      ReportApiRoutes.TEKMEDI_CARD.EXPORT_HISTORY_CARD,
      data,
    );
  };
  exportHistoryCardPdf = (data): Promise<any> => {
    return this.request.post(
      ReportApiRoutes.TEKMEDI_CARD.EXPORT_HISTORY_CARD_PDF,
      data,
    );
  };

  exportHistoryCardPdfOver = (data): Promise<any> => {
    return this.request.post(
      ReportApiRoutes.TEKMEDI_CARD.EXPORT_HISTORY_CARD_PDF,
      data,
    );
  };
}
